import { stylesType } from './style';

/**
 * 
 */
export interface MenuListProps {
    /** Material-UI  */
    classes?: stylesType;
    /**  className */
    className?: string;
    /**  */
    children?: React.ReactNode;
    /** true false  */
    open?: boolean | undefined;
    /**  */
    menuListItems?: MenuListItem[];
    /** MenuListPlacementEnum.BOTTOM_START */
    placement?: MenuListPlacementEnum;
    /** OpenMenuMouseEventEnum.ON_MOUSE_DOWN */
    openMenuMouseEvent?: OpenMenuMouseEventEnum;
    /**  DOM  */
    container?: Element | React.Component | (() => React.ReactInstance);
    /**  */
    handleOpenParentSubMenuList?: () => void;
    /** / */
    onMenuListOpenClose?: (open: boolean | undefined) => void;
    /**  */
    onClickMenuListItem?: (menuListItemReturn: MenuListItem) => void;
}

/**
 * 
 */
export interface WindowMenuBarState {
    /**  */
    open: boolean | undefined;
    /**  */
    mapOpenSubMenuList: Map<string, boolean>;
    /**  hover  */
    menuItemHasSubHoverkey: string | undefined;
    /** ref  */
    anchorRef: React.RefObject<HTMLDivElement>;
}

/**
 * 
 */
export class MenuListItem {
    /** false */
    splitLine?: boolean;
    /**  key */
    key?: string;
    /**  */
    leftText?: string;
    /**  */
    rightText?: string;
    /**  */
    menuListItems?: MenuListItem[];
}

/**
 * 
 * @prompt 
 */
export enum MenuListPlacementEnum {
    BOTTOM_END = 'bottom-end',
    BOTTOM_START = 'bottom-start',
    BOTTOM = 'bottom',
    LEFT_END = 'left-end',
    LEFT_START = 'left-start',
    LEFT = 'left',
    RIGHT_END = 'right-end',
    RIGHT_START = 'right-start',
    RIGHT = 'right',
    TOP_END = 'top-end',
    TOP_START = 'top-start',
    TOP = 'top'
}

/**
 * 
 */
export enum OpenMenuMouseEventEnum {
    ON_MOUSE_DOWN = 'onMouseDown',
    ON_MOUSE_ENTER = 'onMouseEnter',
    ON_CLICK = 'onClick',
    ON_MOUSE_UP = 'onMouseUp'
}
